<template>
  <div>
    <v-card width="100%">
      <v-list>
        <v-list-item-group>
          <v-list-item v-if="weaponDB === undefined">
            <v-list-item-content>
              <v-list-item-title>
                <v-progress-circular
                  indeterminate
                  color="primary"
                  class="ma-3"
                ></v-progress-circular>
                読み込み中
              </v-list-item-title>
            </v-list-item-content>
          </v-list-item>

          <v-list-item v-else-if="ownedWeapon.length === 0">
            <v-list-item-content>
              <v-list-item-title> なし </v-list-item-title>
            </v-list-item-content>
          </v-list-item>

          <v-list-item v-else v-for="weapon in ownedWeapon" :key="weapon.m_ID">
            <v-list-item-icon class="ma-0 mt-1">
              <v-img
                :src="
                  './mergedcharaicon/charaicon_' +
                  weapon.m_EquipableCharaID +
                  '.png'
                "
                width="40px"
                height="40px"
              ></v-img>
            </v-list-item-icon>

            <v-list-item-icon class="ma-1 mt-1">
              <v-img
                :src="
                  'https://texture-asset.kirafan.cn/weaponicon/weaponicon_wpn_' +
                  weapon.m_ResourceID_R +
                  '.jpg'
                "
                width="40px"
                height="40px"
              ></v-img>
            </v-list-item-icon>

            <v-list-item-icon class="ml-n1 mb-0 mt-1">
              <span v-if="weapon.m_EvolvedCount === 3">
                <svg style="width: 40px; height: 40px" viewBox="0 0 24 24">
                  <path
                    fill="gray"
                    d="M15,10.5A1.5,1.5 0 0,1 13.5,12C14.34,12 15,12.67 15,13.5V15C15,16.11 14.11,17 13,17H9V15H13V13H11V11H13V9H9V7H13C14.11,7 15,7.89 15,9M19,3H5C3.91,3 3,3.9 3,5V19A2,2 0 0,0 5,21H19C20.11,21 21,20.1 21,19V5A2,2 0 0,0 19,3Z"
                  />
                </svg>
              </span>
              <span v-else-if="weapon.m_EvolvedCount === 4">
                <svg style="width: 40px; height: 40px" viewBox="0 0 24 24">
                  <path
                    fill="gray"
                     d="M19,19H5V5H19M19,3H5A2,2 0 0,0 3,5V19A2,2 0 0,0 5,21H19A2,2 0 0,0 21,19V5A2,2 0 0,0 19,3M13,17H15V7H13V11H11V7H9V13H13"
                  />
                </svg>
              </span>
            </v-list-item-icon>

            <!-- 
                https://texture-asset.kirafan.cn/weaponicon/weaponicon_wpn_3212200.jpg
             -->
            <v-list-item-content>
              <v-list-item-title
                v-text="weapon.m_WeaponName.slice(2).replace(/専用.*$/, '')"
              >
              </v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-card>
  </div>
</template>

<script>
// import axios from "axios";
export default {
  name: "SPweapon",

  data: () => ({
    weaponDB: undefined,
    // ownedWeapon: [],
  }),

  props: {
    charaList: {
      type: Array,
    },
    getObjChara: {
      type: Function,
    },
    getCheckerSettings: {
      type: Function,
    },
  },

  computed: {
    ownedWeapon() {
      if (this.weaponDB) {
        let output = [];
        const objChara = Object.keys(this.getObjChara())
          .filter((x) => x)
          .map((x) => x.slice(0, -2));

        objChara.map((chara) => {
          //   console.log(chara);
          const index_evoluted4 = this.weaponDB.findIndex(
            (x) => x.m_ID === Number(chara + 4)
          );
          if (index_evoluted4 !== -1) {
            output.push(this.weaponDB[index_evoluted4]);
          } else {
            const index_evoluted3 = this.weaponDB.findIndex(
              (x) => x.m_ID === Number(chara + 3)
            );
            if (index_evoluted3 !== -1) {
              output.push(this.weaponDB[index_evoluted3]);
            }
          }
        });

        return output;
      } else {
        // this.$window.alart("エラー")
        return [
          {
            m_WeaponName: "エラー！@y52enにスクショを添えて報告してください",
            m_ID: "error",
          },
        ];
      }
    },
  },

  async mounted() {
    this.weaponDB = await (
      await fetch("https://database.kirafan.cn/database/WeaponList.json")
    ).json();
    // console.log(this.weaponDB);
    // console.log(this.getObjChara());
  },
};
</script>